import React from "react";
import "./Project.css";

const Project = (props) => {
  return (
    <div
      className="Project"
      onClick={() => {
        window.open(props.link);
        console.log(props.history);
      }}
    >
      <img src={props.image} alt="broken"></img>
      <p>{props.title}</p>
    </div>
  );
};

export default Project;
