import React from "react";
import "./Navbar.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <div
      className="navbar"
      //   style={{
      //     backgroundImage: "url(" + background + ")",
      //   }}
    >
      <div className="nav-link">
        <NavLink to="/portfolio/hello" > <i className="fas fa-home"></i> Hello</NavLink>
      </div>
      <div className="nav-link">
        <NavLink to="/portfolio/projects" > <i className="fas fa-project-diagram"></i>Projects</NavLink>
      </div>
      <div className="nav-link">
        <NavLink to="/portfolio/contact"> <i className="far fa-id-card"></i> Contact me</NavLink>
      </div>
    </div>
  );
};

export default Navbar;
