import React, { useState, useEffect } from "react";
import "./Home.css";
import { useSpring, animated } from "react-spring";
import useWindowDimensions from "./CustomHooks/useWindowDimensions";

const Home = () => {
  const { height, width } = useWindowDimensions();
  console.log(height, width);
  const [hello, setHello] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setHello(false);
    }, 2500);
  }, []);
  const mainProps = useSpring({
    to: { opacity: 1, marginLeft: 0 },
    from: { opacity: 0, marginLeft: width > 700 ? 500 : 0 },
    config: { duration: 1000 },
    delay: 2500,
  });

  const helloProps = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { duration: 2000 },
  });

  if (hello) {
    return (
      <animated.div style={helloProps}>
        <div className="centered-div">
          <h1 className="welcome-message">
            Welcome to my Portfolio <i class="fas fa-book-reader"></i>
          </h1>
        </div>
      </animated.div>
    );
  } else {
    return (
      <animated.div style={mainProps}>
        <div className="Home">
          <div className="main-block">
            <div className="upper-block">
              <div className="home-info">
                <p>Hello, I am</p>
                <h1>Mohammed Saad</h1>
                <p>A Software Engineer | Web and app developer</p>
              </div>
              <img src={require("./images/me.jpg")} alt="hello"></img>
            </div>
            <div className="lower-block">
              <div className="left-block"></div>
              <div className="right-block">
                <div className="recent-frameworks">
                  <p className="up-left">Here are some of the</p>
                  <h1>Recent Frameworks</h1>
                  <p className="down-right">I've worked with</p>
                </div>

                <div>
                  <div className="technology">
                    <p>ReactJS</p>
                    <i className="fab fa-react"></i>
                  </div>
                  <div className="technology">
                    <p>NodeJS</p>
                    <i className="fab fa-node-js"></i>
                  </div>
                  <div className="technology">
                    <p>Flutter</p>
                    <i className="fas fa-mobile-alt"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </animated.div>
    );
  }
};

export default Home;
