import React from "react";
import "./Projects.css";
import "./Project";
import Project from "./Project";

const Projects = (props) => {
  return (
    <div className="Projects">
      <div className="main-block">
        <Project
          {...props}
          link="https://createandshare.netlify.app/"
          title="CreateANDShare"
          image={require("./images/cands.png")}
        />

        <Project
          {...props}
          link="https://izihandmade.netlify.app/"
          title="izihandmade"
          image={require("./images/izi.png")}
        />
        <Project
          {...props}
          title="Spotify Clone"
          link="https://github.com/FatemaFawzy/Frontend-Team"
          image={require("./images/spotify.png")}
        />
      </div>
    </div>
  );
};

export default Projects;
